@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
  font-family: "Rowdies";
  src: url("../public/Rowdies.ttf");
}

@font-face {
  font-family: "Barlow";
  src: url("../public/Barlow.ttf");
}

@font-face {
  font-family: "Lalezar";
  src: url("../public/Lalezar.ttf");
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
